#top-body {
    display:flex;
    justify-content: space-between;
}

.appdivs {
    max-width: 20%;
}


input, select {
    display:block;
}



#pendingTx {
    position:fixed;
    padding:0;
    margin:0;

    top:0;
    left:0;

    width: 100%;
    height: 100%;
    background:rgba(255,255,255,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

#hide {
    display: none;
}