#top-header {
    height: 100px;
    background-color: #0df;
}

nav {
    height: 100px;
    display:flex;
    justify-content: space-between;
}

ul {
    display:flex;
    justify-content: right;
    list-style-type: none;
    margin: 0;
    padding: 0;
}
  
li {
    display: table;
    float: right;
    padding: 0 60px 0 0;

}
  
img {
    height: 100px;
    padding: 0;
}
  
  
.linkitem, .active {
    display: table-cell;
    vertical-align: middle;
    font-size: 40px;
}

#wcselect {
    display: table-cell;
    vertical-align: middle;
    padding-top: 33px;
    font-size: 40px;
    background: none;
    border: none;
    -o-appearance: none;
   -ms-appearance: none;
   -webkit-appearance: none;
   -moz-appearance: none;
   appearance: none;
   outline: 0;
}

#wcselect > option {
    background-color: #0df;
}